import React from 'react'
import { Link } from 'react-router-dom'
import { useSetPoint } from '../Store.jsx';

const Top = () => {
  const Point = useSetPoint((state)=>state.point)
  return (
    <div>
        <div></div>
        <Link  to='/'></Link>
        <a href='/'>2</a>
        <div>{Point}</div>
    </div>
  )
}

export default Top